import React, { Component } from 'react'
import { Link } from 'react-router-dom';

import styled from 'styled-components';

const H1=styled.h1`
    font-size:4rem;
    color:white;
    text-align:center;
    
    `

// const Maindiv=styled.div`
// background-color:blue;
// margin-top:15vh;
// width
// display:flex;
// flex-direction:colomn;
// `
    

export default class Login extends Component {
  constructor(props){
    super(props);
    this.state={
      username:"",
      password:"",
      passvisibility:false,
    };
    this.handleview = this.handleview.bind(this);
  }


  handleSubmit = (e) => {
    e.preventDefault(); 
    console.log('Submitted Username:', this.state.username);
    console.log('Submitted Password:', this.state.password);
    this.setState({
      password:'',
      username:'',
  });
  };

  handleUsernameChange = (e) => {
    this.setState({ username: e.target.value });
  };

  handlePasswordChange = (e) => {
    this.setState({ password: e.target.value });
  };

  handleview(){
    this.setState((prevState) => ({
      passvisibility: !prevState.passvisibility,
    }));
  }
    
  render() {
    return (
      <div>
        <H1>Login Page</H1>

        <div > 
        <form onSubmit={this.handleSubmit} className='mdiv'>
    
            <input
              type="text"
              value={this.state.username}
              onChange={this.handleUsernameChange}
              placeholder='Username'
            />
           
            <input
              type={this.state.passvisibility ?'text':'password'}
              value={this.state.password}
              onChange={this.handlePasswordChange}
              placeholder='Password'
            />

            <button className='hbtn' type='button' onClick={this.handleview}>
              {this.state.passvisibility?<img className='hideimg' src={process.env.PUBLIC_URL + '/hide1.png'} alt="hide" /> : <img className='hideimg' src={process.env.PUBLIC_URL + '/view.png'} alt='Show' />}
            </button>
    
          <button type="submit" className='btn'>LOGIN</button>

          <Link to='/signup' className='l'> 
          <button type='button' className='btn2'>SIGN UP</button>
          </Link>

        </form>
        </div>

      </div>
    )
  }
}
