import React, { Component } from "react";

export default class Signup extends Component {
  constructor(props) {
    super(props);
    this.state = {
      name: "",
      email: "",
      newUser: "",
      newPass: "",
      num:"",
      showPass: false,
    };
    this.passbtn=this.passbtn.bind(this);
  }


  handleName=(e)=> {
    this.setState({name:e.target.value});
  }

  handleNewEmail=(e)=>{
    this.setState({email:e.target.value})
  }

  handleNewUser=(e)=>{
    this.setState({newUser:e.target.value})
  }
  handlePass=(e)=>{
    this.setState({newPass:e.target.value})
  }
  handleNum=(e)=>{
    this.setState({num:e.target.value})
  }

  passbtn(){
    if(this.state.showPass===false){
        this.setState({showPass:true});
    }
    else{
        this.setState({showPass:false});
    }
  }

  handleSignup=(e)=>{
    e.preventDefault();
    console.log("Name :",this.state.name);
    console.log("Email :",this.state.email);
    console.log("Contact Number :",this.state.num);
    console.log("Username :",this.state.newUser);
    console.log("Password :",this.state.newPass);
    

    this.setState({
        name:"",
        email:"",
        num:"",
        newUser:"",
        newPass:"",
    })
  }

  render() {
    return (
      <div>

        <img id="simg" src={process.env.PUBLIC_URL +'/user.png'} alt="" height={'100px'} width={'100px'} />

        
        <form onSubmit={this.handleSignup} className="sdiv">
          <input
            type="text"
            onChange={this.handleName}
            value={this.state.name}
            placeholder="Your Name"
          />

          <input
            type="email"
            onChange={this.handleNewEmail}
            value={this.state.email}
            placeholder="Email Address"
          />

<input
            type="tel"
            onChange={this.handleNum}
            value={this.state.num}
            placeholder="Contact No."
          />

          <input
            type="text"
            onChange={this.handleNewUser}
            value={this.state.newUser}
            placeholder="Pick a Username"
          />

          <input
            type={this.state.showPass ?'text':'password'}
            onChange={this.handlePass}
            value={this.state.newPass}
            placeholder="Password"
          />
          <button className="hbtn" type="button" onClick={this.passbtn}>{this.state.showPass?<img className='hideimg' src={process.env.PUBLIC_URL + '/hide1.png'} alt="hide" /> : <img className='hideimg' src={process.env.PUBLIC_URL + '/view.png'}  alt="show"/>}</button>

          <button className="btn" type="submit">Sign Up</button>
        </form>
      </div>
    );
  }
}
